/**
 * AI Configuration Types
 *
 * Type definitions for the AI selection and configuration system
 */

import { AIDifficulty } from './game-types';

/**
 * AI difficulty levels for categorization
 */
export type AILevel = 'beginner' | 'intermediate' | 'advanced' | 'professional';

/**
 * AI strength rating (1-5 stars)
 */
export type AIStrength = 1 | 2 | 3 | 4 | 5;

/**
 * AI personality types that affect play style
 */
export type AIPersonality = 'balanced' | 'aggressive' | 'defensive' | 'positional';

/**
 * AI configuration option
 */
export interface AIOption {
  /** Unique identifier */
  id: AIDifficulty;

  /** Display name */
  name: string;

  /** Difficulty category */
  level: AILevel;

  /** Star rating (1-5) */
  strength: AIStrength;

  /** Short description */
  description: string;

  /** Estimated thinking time */
  thinkingTime: string;

  /** Search depth (for minimax variants) */
  searchDepth: number | string;

  /** Special features/capabilities */
  features: string[];

  /** Whether this is the default for its level */
  isDefault?: boolean;

  /** Whether this option supports customization */
  isCustomizable?: boolean;
}

/**
 * Custom AI settings for advanced users
 */
export interface AICustomSettings {
  /** Thinking time in seconds */
  thinkingTime: number;

  /** Search depth */
  searchDepth: number;

  /** Use opening book */
  useOpeningBook: boolean;

  /** Use endgame solver */
  useEndgameSolver: boolean;

  /** Adaptive time management */
  adaptiveTime: boolean;

  /** AI personality */
  personality: AIPersonality;

  /** Enable iterative deepening */
  iterativeDeepening: boolean;

  /** Use transposition table */
  useTranspositionTable: boolean;
}

/**
 * AI selection state
 */
export interface AISelectionState {
  /** Currently selected level */
  currentLevel: AILevel;

  /** Selected AI option */
  selectedAI: AIDifficulty | null;

  /** Custom settings if applicable */
  customSettings?: AICustomSettings;

  /** Whether advanced options are expanded */
  showAdvanced: boolean;
}

/**
 * Default custom settings
 */
export const DEFAULT_AI_CUSTOM_SETTINGS: AICustomSettings = {
  thinkingTime: 5,
  searchDepth: 6,
  useOpeningBook: true,
  useEndgameSolver: true,
  adaptiveTime: true,
  personality: 'balanced',
  iterativeDeepening: true,
  useTranspositionTable: true,
};

