/**
 * Get AI option by ID
 */
export function getAIOption(id: AIDifficulty): AIOption | null {
  for (const level of Object.values(AI_OPTIONS_DATABASE)) {
    const option = level.find(ai => ai.id === id);
    if (option) return option;
  }
  return null;
}

/**
 * Get default AI for a level
 */
export function getDefaultAIForLevel(level: AILevel): AIOption | null {
  const options = AI_OPTIONS_DATABASE[level];
  return options.find(ai => ai.isDefault) || options[0] || null;
}

/**
 * Get all available AI levels
 */
export function getAvailableLevels(): AILevel[] {
  return Object.keys(AI_OPTIONS_DATABASE) as AILevel[];
}

/**
 * Get strength stars display
 */
export function getStrengthStars(strength: AIStrength): string {
  return '‚òÖ'.repeat(strength) + '‚òÜ'.repeat(5 - strength);
}

